import * as THREE from "three";

import Stats from "stats.js";
import hasWebgl from "has-webgl";
import OrbitControls from "three-orbitcontrols";

import { modelList } from "./model-manager";
import GLTFLoader from "three-gltf-loader";

// initialize globals
let scene, camera, renderer, loader, controls, clock, mixerUpdateDelta;
let mixers = [];
const models = modelList;
//console.log(modelList);

const init = () => {
  scene = new THREE.Scene();

  camera = new THREE.PerspectiveCamera(
    50,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );

  renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.shadowMap.enabled = true;
  document.body.appendChild(renderer.domElement);

  loader = new GLTFLoader();
  loader.load("Designer.glb", gltf => {
    console.log("hi")
    scene.add(gltf.scene);
  });
  clock = new THREE.Clock();

  controls = new OrbitControls(camera, renderer.domElement);
  controls.enablePan = true;

  camera.position.set(20, 5, -20);
  camera.lookAt(0, 0, 0);
  window.addEventListener("scroll", e => {
    e.preventDefault();
    console.log(scene);
    //scene.children[3].rotation.y = e.value
  });
  window.addEventListener("resize", onWindowResize, false);

  //loadAllModels();
  addLights();
  animate();
};

const loadAnimation = (model, animation) => {
  let currentMixer = new THREE.AnimationMixer(model);
  let clip = currentMixer.clipAction(animation);
  mixers.push(currentMixer);
  clip.play();
};

const addLights = () => {
  let hemiLight = new THREE.HemisphereLight(0x2174ff, null, 0.25);
  hemiLight.position.set(5, 50, 0);
  scene.add(hemiLight);

  let ambientLight = new THREE.AmbientLight(0xffffff, 0.2);
  ambientLight.position.set(0, 5, 0);
  scene.add(ambientLight);

  let dirLight = new THREE.DirectionalLight(0x2174ff, 1.5);
  dirLight.position.set(8, 30, -40);
  dirLight.castShadow = true;
  dirLight.shadow.mapSize.width = 2048; // default
  dirLight.shadow.mapSize.height = 2048; // default
  dirLight.shadow.camera.near = 0.1; // default
  dirLight.shadow.camera.far = 2000; // default
  scene.add(dirLight);

  // const dirLightHelper = new THREE.DirectionalLightHelper(dirLight, 5);
  // scene.add(dirLightHelper);

  // const dirLightCamHelper = new THREE.CameraHelper(dirLight.shadow.camera);
  // scene.add(dirLightCamHelper);
};

const animate = () => {
  requestAnimationFrame(animate);
  mixerUpdateDelta = clock.getDelta();
  for (let i = 0; i < mixers.length; i++) {
    mixers[i].update(mixerUpdateDelta);
  }
  renderer.render(scene, camera);
};

const onWindowResize = () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
};

init();
